import React from "react";
import MemoryImg from "../components/MemoryImg/MemoryImg";
import IMG_LIST from "../components/LINK__IMG";
import EventName from "../components/EventName/EventName";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Event1 from "./EventImgComponents/Event1";
import Event2 from "./EventImgComponents/Event2";
import Event3 from "./EventImgComponents/Event3";

export default function ImgContain() {
     return (
          <Router>
               <EventName />
               <Route exact path="/Images" component={Event1} />
               <Route exact path="/tetyeuthuong" component={Event2} />
               <Route exact path="/promnight" component={Event3} />
          </Router>
     );
}
